<template lang="html">
  <div class="group-setting">
    <BaseSetting @on-click="handleGroupSetting" />
    <!--  -->
    <el-dialog :visible.sync="dialogVisible" title="" width="320px" center>
      <div>
        <el-select v-model="innerGroupId" placeholder="" size="small">
          <el-option
            v-for="item in enableOptions"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          />
        </el-select>
        <span slot="footer">
          <el-button @click="handleGroupChange" type="primary" size="small"></el-button>
        </span>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import BaseSetting from '@/components/base/BaseSetting'

export default {
  name: 'UserStatusSetting',
  components: {
    BaseSetting
  },
  props: {
    // id JavaScript
    userId: {
      type: String,
      required: true
    },
    groupId: {
      type: String,
      required: true
    }
  },
  data () {
    return {
      dialogVisible: false,
      enableOptions: [{ label: '', value: '0' }, { label: '', value: '1' }],
      innerGroupId: this.groupId,
      groupOptions: []
    }
  },
  methods: {
    // 
    handleGroupSetting () {
      this.dialogVisible = true
    },
    // idnameidgroudNames
    handleGroupChange () {
      if (this.groupId !== this.innerGroupId) {
        this.$httpAPI.rebateStatus({
          params: { rebateId: this.userId, status: this.innerGroupId }
        }).then(() => {
          const groupObject = this.$_.find(this.enableOptions, item => item.value === this.innerGroupId)
          this.$emit('on-change', Object.assign({ userId: this.userId }, groupObject))
        }).catch(error => console.log(error))
      }
      this.dialogVisible = false
    },
    fetchUserGroup () {
      const length = this.$store.state.user.usersGroup.length

      // vuex
      if (!length) {
        this.$httpAPI.getSystemGroup({ params: { pageNo: 1, pageSize: 100 } })
          .then(response => {
            // UI
            this.$_.forEach(response.data.data, item => {
              this.groupOptions.push({
                value: String(item.groupId),
                label: item.name
              })
            })

            this.$store.dispatch('addUsersGoup', this.groupOptions) // vuexhttp
          })
          .catch(error => console.log(error))
      } else {
        this.groupOptions = this.$store.state.user.usersGroup
      }
    }
  }
}
</script>

<style lang="scss" scoped>
  .el-button {
    margin-left: 10px;
  }
</style>
