<template lang="html">
  <div class="dialog-limit-form">
    <el-dialog :visible.sync="dialogVisible" title="" width="760px">
      <el-form :model="formData" label-width="100px" size="small">
        <el-row>
          <el-col :span="12">
            <el-form-item label="ID">
              <el-input :value="formData.userId" disabled />
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="">
              <el-input :value="formData.username" disabled />
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="">
              <el-input :value="formData.nickname" disabled />
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="">
              <el-input :value="formData.money | RMB" disabled />
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="">
              <el-input :value="formData.beforeMoney | RMB" disabled />
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="">
              <el-input :value="formData.afterMoney | RMB" disabled />
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="ID">
              <el-input :value="formData.target" disabled />
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="">
              <el-input :value="formData.targetName" disabled />
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="ID">
              <el-input :value="formData.createId" disabled />
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="">
              <el-input :value="formData.createName" disabled />
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="">
              <el-input :value="formData.createTime | time" disabled />
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="">
              <el-input :value="formData.type ? '' : ''" disabled />
            </el-form-item>
          </el-col>
          <el-col :span="24">
            <el-form-item label="">
              <el-input :value="formData.remark" type="textarea" rows="3" disabled />
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
      <span v-if="formData.status <= 1" slot="footer">
        <el-button @click="changeStatus(2)" type="primary" size="small"></el-button>
        <el-button @click="changeStatus(3)" type="danger" size="small"></el-button>
        <el-button v-if="formData.status !== 1" @click="changeStatus(1)" size="small"></el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: 'DialogLimitForm',
  props: {
    formData: {
      type: Object,
      required: true
    }
  },
  data () {
    return {
      dialogVisible: false
    }
  },
  methods: {
    toggleDialogVisible (status) {
      this.dialogVisible = status
    },
    changeStatus (status) {
      this.$httpAPI.updateFinanceLimitChangeStatus({
        params: { convertId: this.formData.convertId, status }
      }).then(() => {
        this.$emit('on-success', { convertId: this.formData.convertId, status })
        this.dialogVisible = false
        this.$message.success('')
      }).catch(error => console.log(error))
    }
  }
}
</script>
