<template>
  <div>
    <el-input v-model="level" placeholder="" style="width: 160px;margin-bottom: 15px"></el-input>
    <el-button type="primary" icon="el-icon-search" style="margin-left: 10px" @click="sumbit"></el-button>
    <el-button type="primary" icon="el-icon-plus" style="float: right" @click="dialogVisible = true"></el-button>
    <el-table
      :data="tableData"
      size="small"
      highlight-current-row
      border
    >
      <el-table-column type="index" :min-width="30" />
      <el-table-column prop="name" label="">
        <template slot-scope="scope">
          <span>{{scope.row.name}}</span>
        </template>
      </el-table-column>
      <el-table-column prop="upperLimit" label="">
        <template slot-scope="scope">
          <span>{{scope.row.upperLimit | zero}}</span>
        </template>
      </el-table-column>
      <el-table-column prop="lowerLimit" label="">
        <template slot-scope="scope">
          <span>{{scope.row.lowerLimit | zero}}</span>
        </template>
      </el-table-column>
      <el-table-column prop="ratio" label="">
        <template slot-scope="scope">
          <span>{{scope.row.ratio | percentage}}</span>
        </template>
      </el-table-column>
      <el-table-column prop="status" label="">
        <template slot-scope="scope">
          <span>{{scope.row.status | isEnabled}}</span>
          <UserStatusSetting
            @on-change="syncGroupData"
            :userId="String(scope.row.id)"
            :groupId="String(scope.row.status)"
            class="pull-right"
          />
        </template>
      </el-table-column>
      <el-table-column prop="sysGroupNames" label="">
      </el-table-column>
      <el-table-column prop="updateTime" label="" sortable>
        <template slot-scope="scope">
          <span>{{scope.row.updateTime | time}}</span>
        </template>
      </el-table-column>
    </el-table>
    <el-dialog title="" :visible.sync="dialogVisible" width="500px">
      <el-form :model="formData" label-width="120px" ref="limitForm" :rules="rule">
        <el-form-item prop="name" label="">
          <el-input v-model="formData.name" placeholder="" />
        </el-form-item>

        <el-form-item prop="rebateUserGroups" label="">
          <el-select v-model="formData.rebateUserGroups" placeholder="" style="width: 100%" multiple>
            <el-option v-for="item in groupOptions" :key="item.value" :label="item.label" :value="item.value" />
          </el-select>
        </el-form-item>
        <el-form-item prop="gameConfigId" label="">
          <el-select v-model="formData.gameConfigId" placeholder="" style="width: 100%">
            <el-option v-for="item in gameType" :key="item.id" :label="item.name" :value="item.id" />
          </el-select>
        </el-form-item>
        <el-form-item prop="ratio" label="">
          <el-input v-model="formData.ratio" min="0" placeholder="" />
        </el-form-item>
        <el-form-item prop="upperLimit" label="">
          <el-input v-model="formData.upperLimit" type="number" min="0" placeholder="" />
        </el-form-item>

        <el-form-item prop="lowerLimit" label="">
          <el-input v-model="formData.lowerLimit" type="number" placeholder="" />
        </el-form-item>

        <el-form-item>
          <el-button type="primary" @click="submitForm('limitForm')" style="margin-left: 94px"></el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="currentPage"
      :page-sizes="[10, 20, 30, 40]"
      :page-size="10"
      layout="total, sizes, prev, pager, next, jumper"
      :total="total">
    </el-pagination>
  </div>
</template>

<script type="text/javascript">
import UserStatusSetting from './UserStatusSetting'

export default {
  components: {
    UserStatusSetting
  },
  props: {
    gameConfigId: {
      type: Number
    },
    gameType: {
      type: Array
    }
  },
  data () {
    return {
      level: '',
      tableData: [],
      pageSize: 10,
      currentPage: 1,
      total: 0,
      dialogVisible: false,
      formData: {
        name: '', upperLimit: '', ratio: '', lowerLimit: '', gameConfigId: '', rebateUserGroups: []
      },
      groupOptions: [],
      rule: {
        name: [{ required: true, message: '' }],
        rebateUserGroups: [{ required: true, message: '' }],
        gameConfigId: [{ required: true, message: '' }],
        ratio: [{ required: true, message: '' }],
        upperLimit: [{ required: true, message: '' }],
        lowerLimit: [{ required: true, message: '' }]
      }
    }
  },
  created () {
    this.fetchRebateList()
    this.fetchUserGroup()
  },
  methods: {
    sumbit () {
      this.fetchRebateList()
    },
    handleSizeChange (val) {
      this.pageSize = val
      this.fetchRebateList()
    },
    handleCurrentChange (val) {
      console.log(val)
      this.currentPage = val
      this.fetchRebateList()
    },
    submitForm (formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.$httpAPI.rebateSave(this.formData).then(() => {
            this.$router.push({ name: 'BackwaterSetting' })
            this.$message.success('')
          }).catch(error => console.log(error))
        } else {
          this.$message.warning('')
        }
      })
    },
    // 
    syncGroupData (payload) {
      this.$_.forEach(this.tableData, item => {
        if (String(item.id) === payload.userId) {
          item.status = Number(payload.value)
        }
      })
    },
    // 
    fetchRebateList () {
      this.$httpAPI.rebateList({
        params: {
          name: this.level,
          gameConfigId: this.gameConfigId,
          pageNo: this.currentPage,
          pageSize: this.pageSize
        }
      }).then(response => {
        this.tableData = response.data.data
        this.total = response.data.amount
      }).catch(error => console.log(error))
    },
    // 
    fetchUserGroup () {
      const length = this.$store.state.user.usersGroup.length
      // vuex
      if (!length) {
        this.$httpAPI.getSystemGroup({ params: { pageNo: 1, pageSize: 100 } })
          .then(response => {
            // UI
            this.$_.forEach(response.data.data, item => {
              this.groupOptions.push({
                value: String(item.groupId),
                label: item.name
              })
            })
            this.$store.dispatch('addUsersGoup', this.groupOptions) // vuexhttp
          })
          .catch(error => console.log(error))
      } else {
        this.groupOptions = this.$store.state.user.usersGroup
      }
    }
  }
}
</script>
