<template lang="html">
  <div class="limit-add">
    <el-button
      @click="dialogVisible = true"
      icon="el-icon-circle-plus-outline"
      size="small"
      type="primary"
      class="pull-right"
    ></el-button>

    <el-dialog title="" :visible.sync="dialogVisible" width="500px">
      <el-form :model="formData" :rules="rules" label-width="100px" ref="limitForm">
        <el-form-item prop="username" label="">
          <el-input v-model="formData.username" placeholder="" />
        </el-form-item>

        <el-form-item prop="target" label="">
          <el-select v-model="formData.target" placeholder="" style="width: 100%">
            <el-option v-for="item in gameTypes" :key="item.value" :label="item.label" :value="item.value" />
          </el-select>
        </el-form-item>

        <el-form-item prop="money" label="">
          <el-input v-model="formData.money" type="number" min="0" placeholder="" />
        </el-form-item>

        <el-form-item prop="remark" label="">
          <el-input v-model="formData.remark" type="textarea" :rows="3" placeholder="" />
        </el-form-item>

        <el-form-item>
          <el-button type="primary" @click="submitForm('limitForm')"></el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: 'LimitAdd',
  data () {
    return {
      gameTypes: [],
      formData: {
        username: '', money: '', target: '', remark: ''
      },
      rules: {
        username: [
          { required: true, message: '' },
          { min: 2, max: 20, message: ' 2 - 20 ' }
        ],
        target: { required: true, message: '' },
        money: [
          { required: true, message: '' }
        ]
      },
      dialogVisible: false
    }
  },
  created () {
    this.fetchGamesList()
  },
  methods: {
    handleCreatedLoadItem (load) {
      this.dialogVisible = false
      this.$emit('on-success', load)
    },
    submitForm (formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.$httpAPI.createFinanceLimitChange(this.formData).then(() => {
            this.$emit('on-success')
            this.dialogVisible = false
            this.formData = { username: '', money: '', target: '', remark: '' }
            this.$message.success('')
          }).catch(error => console.log(error))
        } else {
          this.$message.warning('')
        }
      })
    },
    fetchGamesList () {
      this.$httpAPI.fetchGamesList().then(response => {
        this.$_.forEach(response.data.data, item => {
          this.gameTypes.push({
            label: item.name,
            value: item.id
          })
        })
      }).catch(error => console.log(error))
    }
  }
}
</script>
